@import url(https://fonts.googleapis.com/css2?family=Lato&family=Quicksand:wght@500&family=Viga&display=swap);
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-family: 'Viga', sans-serif;
    */
    font-family: 'Quicksand', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.mt60{
  margin-top: 60px;
}
.pt120{
  padding-top: 125px;
}
h1, h2, h3, h4, h5, h6{
  font-weight: bolder !important;
  color: black;
}

.form-control{
  border-radius: 0 !important;
  outline: 0 !important;
  border: 0 !important;
  border-bottom: 2px solid #007bff !important;
  font-weight: bold !important;
}
.form-control:focus{
  border:none;
  box-shadow: none !important;
 -webkit-box-shadow: none !important;
 -moz-box-shadow: none !important;
 -moz-transition: none !important;
 -webkit-transition: none !important;
}
.btn{
  border-radius: 100px;
  font-weight: bold;
  border-width: 0.15em;
}
a{
  color: #007bff !important;
  font-weight: bolder !important;
}
.text-small{
  font-size: 12px;
}
.img-preview{
  width: auto;
  height: auto;
  max-width: 250px;
  max-height: 250px;
  border-radius: 10px;
}
.tabdiv{
  position: fixed;
  top:0;
  left: 50%;
  height: 100vh;
  width: 100vw;
  max-width: 650px;
  overflow-y: auto;
  padding-left: 6vw;
  padding-right: 6vw;
  transform: translateX(-50%);
  z-index: 1;
}

.truncate{
  width: 100%;
}
.truncate2{
  width: 80%;
}
.collaspe{
  display: none !important;
}
.text-black{
  color: black;
}
.Toastify__toast-body{
  font-weight: bold !important;
}
.app-div
{
  max-width: 700px;
  min-height: 100vh;
  padding-left: 6vw;
  padding-right: 6vw;
  background-color:white;
}

.icon{
  color: black;
}
.header{
    position: fixed;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 99;
    background-color: white;
    width: 100vw;
    max-width: 680px;
}
.logo{
    font-size: 1.9em;
    font-weight: bold;
    font-family: 'Viga', sans-serif;
}
.headtwo{
    font-size: 1.5em;
    font-weight: bolder;
    
}
.add-btn{
    z-index: 99 !important;
    height: 60px;
    width: 60px;
    border-radius: 15px;
    position: fixed;
    bottom: 15px;
    right: 15px;
    box-shadow: 0 4px 14px 0 rgba(0,0,0,0.1);
    background-color: #ffffff;
}
.add-icon{
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
   
  
}
.togglerbg{
    padding: 5px;
    border-radius: 100px;
    display: inline-block;
}
.filterbtnbg{
    padding: 5px;
    border-radius: 100px;
    display: inline-block;
    float: right;
}
.filterdiv{
    border-radius: 1.5em;
    box-shadow: 0 0px 0px 1px rgba(0,0,0,0.05);
    padding: 15px;
    margin-bottom: 0.5cm;
}
.post-div{
    border-radius: 1.5em;
    box-shadow: 0 0px 0px 1px rgba(0,0,0,0.05);
    padding: 10px;
    margin-bottom: 0.5cm;
}
.post-dp{
    width: 1.2cm;
    height: 1.2cm;
    border-radius: 50%;
}
.post-pic{
    width: 100%;
    border-radius: 1.5em;
    margin-top: 0.15cm;
}
.fs16{
    font-size: 15px;
}
.contact-info{
    border-radius: 1.5em;
    box-shadow: 0 0px 0px 1px rgba(0,0,0,0.05);
}
.loadingdiv{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vw;
    background-color: white;
}
.lodingcenterdiv{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.location-text{
    font-size: 14px;
}
.userdp{
    width: 100%;
    border-radius: 50%;
}
.dp{
    width: 35vw;
    height: 35vw;
    border-radius: 50%;
    max-width: 120px;
    max-height: 120px;
    border: 0px solid black;
}
.App {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  
  .crop-container {
    max-width: 120px;
    max-height: 120px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 100px;
  }
